<template>
  <div>
    <div
    @click="openGallary"
    class="banner">
      <img class="banner-img" src="//img1.qunarzz.com/sight/p0/1411/b6/662fc49d1d26a5a7775cf6b7f8b4ecec.water.jpg_600x330_c34588e7.jpg" alt="">
      <div class="banner-info">
        <div class="banner-desc">汉秀剧场</div>
        <div class="banner-num">
          <span class="iconfont icon-left"></span>
          39
        </div>
      </div>
    </div>
    <common-gallary
    v-show="isGallaryShow"
    :gallaryImgs="gallaryImgs"
    @closeGallary="closeGallary"></common-gallary>
  </div>
</template>
<script>
import CommonGallary from 'common/gallary/gallary'
export default {
  data () {
    return {
      isGallaryShow: false,
      gallaryImgs: [
        'http://img1.qunarzz.com/sight/p0/1411/c0/510cdcb852590824eb40027bb011c18a.water.jpg_r_800x800_5e94080e.jpg',
        'http://img1.qunarzz.com/sight/p0/1505/10/10d0ebb75a00502b.water.jpg_r_800x800_11d6710e.jpg',
        'http://img1.qunarzz.com/sight/p0/1505/10/10d0ebb75a00502b.water.jpg_r_800x800_11d6710e.jpg'
      ]
    }
  },
  components: {
    CommonGallary
  },
  methods: {
    openGallary () {
      this.isGallaryShow = true
    },
    closeGallary () {
      this.isGallaryShow = false
    }
  }
}
</script>
<style lang="scss" scoped>
  .banner {
    height: 412px;
    position: relative;
    .banner-img {
      width: 100%;
      height: 100%;
      display: block;
    }
    .banner-info {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      color: #fff;
      padding: 0 20px;
      align-items: center;
      .banner-desc {
        flex-grow: 1;
        height: 60px;
        line-height: 60px;
        font-size: 32px;
      }
      .banner-num {
        padding: 10px;
        font-size: 24px;
        border-radius: 34px;
        background: rgba(0, 0, 0, 0.8);
      }
    }
  }
</style>



