<template>
  <div
  class="container"
  @click="handleClickGallary">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide
        v-for="(imgs, ind) of gallaryImgs"
        :key="ind">
          <img
          class="gallary-img"
          :src="imgs"
          alt="">
        </swiper-slide>
        <div class="swiper-pagination gallary-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observer: true,
        observeParents: true
      }
    }
  },
  props: {
    gallaryImgs: {
      type: Array,
      default () {
        return [
          'http://img1.qunarzz.com/sight/p0/1411/c0/510cdcb852590824eb40027bb011c18a.water.jpg_r_800x800_5e94080e.jpg',
          'http://img1.qunarzz.com/sight/p0/1505/10/10d0ebb75a00502b.water.jpg_r_800x800_11d6710e.jpg'
        ]
      }
    }
  },
  methods: {
    handleClickGallary () {
      this.$emit('closeGallary')
    }
  }
}
</script>

<style lang="scss" scoped>
  .container /deep/ .swiper-container {
    overflow: inherit;
  }
  .container {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 100;
    color: #fff;
    .wrapper {
      width: 100%;
      .gallary-img{
        width:100%;
      }
      .gallary-pagination {
        bottom: -100px;
      }
    }
  }
</style>



